
#info_section {
    border: 1px solid grey;
    background-color: black;
    height: 5000px;
    flex-grow: 0.7;
    animation-name: info_intro;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
}

@keyframes info_intro {
    from {
        transform: translateX(-17vh) rotate(-10deg);
        filter: blur(10px);
    }
    to {
        transform: translateX(0);
        filter: blur(0);
    }
}