
#header {
    background-color: black;
    color: white;
    display: flex;
    flex-direction: row;
    padding-bottom: 15px;
    padding-top: 10px;
    flex-wrap: wrap;
    border: 1px solid white;
    justify-content: center;
    align-items: center;
    height: fit-content;
    animation-name: header_intro;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    z-index: 999;
}

@media (max-width: 780px) {
    #logo {
        display: none;
    }
    .header_button {
        margin-left: 40px !important;
        animation-duration: 1s;
        animation-timing-function: ease-in-out;
        animation-name: buttons_loading;
    }
}

@keyframes header_intro {
    from {
        transform: translateY(-17vh) rotate(-10deg);
        filter: blur(10px);
    }
    to {
        transform: translateY(0);
        filter: blur(0);
    }
}

@keyframes buttons_loading {
    from {
        transform: translateY(-17vh);
        filter: blur(100px);
    }
    to {
        transform: translateY(0);
        filter: blur(0);
    }
}